import React, { useEffect, useState } from 'react'
import style from './Detailes.module.css'
import save from '../../../../assets/images/save.png'
import { SlClose } from 'react-icons/sl'
import classNames from 'classnames'
import { Formik, Form, Field } from 'formik'
import { Priority } from './Priorety/Priorety'
function makeId(length) {
  var result = ''
  var characters =
    'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789'
  var charactersLength = characters.length
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength))
  }
  return result
}
export const Detailes = ({ todo, setTodo, setIsTodo, isActive, setNowDay }) => {
  const [task, setTask] = useState('')
  const [error, setError] = useState('')
  useEffect(() => {
    setError('')
  }, [task])
  useEffect(() => {
    setIsTodo(false)
  }, [task])
  {
    if (isActive) {
      return (
        <div className={style.detailesOpen}>
          <div className={style.ditalesC}>
            <SlClose
              className={style.close}
              onClick={() => {
                setIsTodo(false)
              }}
            />
            <h2 className={style.title}>Detailes</h2>
            <div>
              <Formik
                initialValues={{
                  nowDay: 'Not for one day)',
                  todo: '',
                  priority: 'What`s about priorety?',
                }}
                onSubmit={(values, actions) => {
                  const addNewTask = (whatToDo) => {
                    setTodo([
                      {
                        id: makeId(10),
                        isCompleted: false,
                        whatToDo,
                        currentDay: values.nowDay,
                        priority: values.priority,
                      },
                      ...todo,
                    ])
                  }
                  if (values.todo !== '') {
                    setTask(values.todo)
                    addNewTask(values.todo)
                  } else {
                    setError('iNPUT YOUR TODO')
                  }
                }}
              >
                {(props) => (
                  <Form>
                    <div className={style.containerInput}>
                      <Field
                        type="textarea"
                        name="todo"
                        placeholder="What to do"
                        className={style.whatTask}
                      />
                      <div className={style.selectTask}>
                        <button className={style.button} type="submit">
                          <img className={style.img} src={save} alt="" />
                          <span className={style.text}>Save</span>
                        </button>
                        <Field
                          as="select"
                          name="nowDay"
                          className={style.select}
                        >
                          <option value={'Monday'}></option>
                          <option value={'Tuesday'}></option>
                          <option value={'Wednesday'}></option>
                          <option value={'Thirsday'}></option>
                          <option value={'Friday'}></option>
                          <option value={'Saturday'}></option>
                          <option value={'Sunday'}></option>
                        </Field>
                        <Field
                          as="select"
                          name="priority"
                          className={style.select}
                        >
                          <option value={'Low Priorety'}> </option>
                          <option value={'Medium Priorety'}></option>
                          <option value={'High Priorety'}> </option>
                        </Field>
                      </div>
                    </div>
                  </Form>
                )}
              </Formik>
              <h3 className={style.error}>{error}</h3>
            </div>
          </div>
        </div>
      )
    }
  }
  return <div className={style.detailes}></div>
}
